.button {
  font-family: Oswald, sans-serif;
  color: #fff;
  line-height: 18px;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #68B738;
  border: none;
  font-size: 16px;
  &--s {
    font-size: 14px;
  }
  &--grey {
    background-color: #F2F2F2;
    color: #444444;
  }
}
